.main_slide{
    position:relative;
    width:100%;
    height :708px;
}

.main_slide_vertical{
    display:flex;
    position: absolute;
    left:-940px;
}

.main_slide_item{
    position: relative;
    margin:0px 6px 0px 6px;
}

@keyframes text_up{
    0%{
        opacity: 0;
    }
    30%{
        opacity: 0;
        transform: translate3d(0, 40px, 0);
    }
    100%{
        opacity: 1;
    }
}

.main_slide_text_0{
    position:absolute;
    display:flex;
    align-items: center;
    bottom:70px;
    left:50px;
    animation: text_up 1s;
}

.main_slide_text_0 button {
    width:135px;
    height: 46px;
    border-radius: 25px;
    padding:0px;
    display:flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size:13.5px;
    margin-right:40px;
    border:2px solid white;
    background-color: white;
}

.main_slide_text_0 span{
    display:flex;
    align-items: center;
    color:white;
}
.main_slide_text{
    position:absolute;
    display:none;
    align-items: center;
    bottom:70px;
    left:50px;
    animation: text_up 1s;
}

.main_slide_text button {
    width:135px;
    height: 46px;
    border-radius: 25px;
    padding:0px;
    display:flex;
    align-items: center;
