}

.main_slide_btn button:first-child{
    width:316px;
    height: 703px;
    position:absolute;
    top:0px;
    opacity: 0.8;
    border:none;
    cursor: pointer;
}

.main_slide_btn button:last-child{
    width:314px;
    height: 703px;
    position:absolute;
    top:0px;
    right:0px;
    opacity: 0.8;
    border:none;
    cursor: pointer;
}

